.navbar {
  display: flex;
  justify-content: space-between;
  background-color: #0F1121;
  height: 48px;
  box-sizing: border-box;
  border-bottom: 1px solid #3B3E4A;

  @media (min-width: 1200px) {
    height: 64px;
  }

  &__icons, &__links {
    display: flex;
  }

  &__link {
    color: #75767F;
    text-decoration: none;
    text-transform: uppercase;
    line-height: 48px;
    font-size: 12px;
    font-weight: 800;
    margin: 0 16px;
    position: relative;
    display: flex;

    &.active {
      color: #F1F2F9;
    }

    &.active:after {
      content: "";
      width:100%;
      bottom: 0;
      position: absolute;
      border-bottom: 3px solid #F1F2F9;
    }

    @media (min-width: 1200px) {
      line-height: 64px;
      margin: 0 32px;
    }
  }

  &__logo {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 64px;
    padding: 0 16px 0 16px;

    @media (min-width: 1200px) {
      padding: 0 16px 0 24px;
      width: 80px;
    }

    &-text {
      width: 64px;
      @media (min-width: 1200px) {
        width: 80px;
      }
    }

    &-icon {
      position: absolute;
      top: 12px;
      left: 48px;
      height: 12px;
      width: 9px;

      @media (min-width: 1200px) {
        width: 11px;
        height: 14.5px;
        top: 18px;
        left: 64px;
      }
    }
  }

  &__icon {
    display: flex;
    justify-content: center;
    align-items: center;
    border-left: 1px solid #3B3E4A;
    height: 48px;
    width: 48px;
    cursor: pointer;

    @media (min-width: 1200px) {
      height: 64px;
      width: 64px;
    }
  }
}
